import { _decorator, Button, Component, instantiate, Node, Prefab, Animation, Label } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('shoot')
export class shoot extends Component {
    
    @property(Prefab)
    bulletPrefab: Prefab = null;

    @property(Node)
    enemy: Node = null;

    @property(Label)
    hitcount: Label = null;
    // 定義bulletAry為陣列，用於存放擊中事件
    bulletAry:Node[] = [];
    x : Number;

    onLoad() {
        // 將HIT字串設定為空值
        this.hitcount.string = "132";
        // 將bulletAry陣列設定為空值
        this.bulletAry =[];
        this.x = 0;
        
        
    }

    // @property(Button)
    // shoot_btn: Button=null;

    start() {
        // this.shoot_btn.node.on('click', this.onclickshoot,this);
    }

    update(deltaTime: number) {
        for(let i = this.bulletAry.length - 1; i >= 0; i--){
            let addbullet = this.bulletAry[i];
            if(this.hit(addbullet, this.enemy)){
                // 清除陣列中檢查完的擊中事件
                this.bulletAry.splice(i,1);
            };
        }
    }

    onclickshoot(evt){
        console.log("Shoot");
  
        let addbullet = instantiate(this.bulletPrefab);      // 複製bulletPrefab(子彈)
        this.node.addChild(addbullet);        // 在場景中新增子彈節點
        let bulletAni = addbullet.getComponent(Animation);        // 子彈消失
        // 從場景中移除子彈動畫節點
        bulletAni.on(Animation.EventType.FINISHED, () => {
            addbullet.parent = null;
            console.log("Miss");
        }, this);

        // 將擊中事件放進陣列
        this.bulletAry.push(addbullet);
    }

    collision(addbullet, enemy){
        // 計算子彈與敵人間的距離
        let dist = addbullet.position.subtract(enemy.position).length();
        // 回傳dist數值
        return dist;

    }


    hit(addbullet, enemy){
        if(this.collision(addbullet, enemy) < 50){
            
            console.log("Hit");
            let bulletAni = addbullet.getComponent(Animation);
            // 停止監聽子彈移動事件
            bulletAni.off(Animation.EventType.FINISHED, () => {}, this); 
            // 將子彈父節點清空
            addbullet.parent = null;

            this.x++;
            // 顯示hit
            this.hitcount.string = "hit:" + this.x;
            // 1秒後hit消失
            this.scheduleOnce(function(){
                this.hitcount.string=""
            }.bind(this),1)

            return true;
        }

    }


}


